<template>
    <div class="myYearCard">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">编辑次卡</span>
            <span class="back"></span>
            <span class="release" @click="release">完成</span>
        </div>
        <div class="content">
            <form class="timeCard">
                  <label class="title">一、内容</label>
                  <textarea class="cardInfo" v-model="card.bundleNote"></textarea>
                  <label class="title">二、价格</label>
                  <span class="price">
                    <label>原&emsp;&emsp;价:</label>
                    <input type="number" class="priceNum" v-model="card.bundleOrigPrice">元/
                    <input type="number" class="times" v-model="card.bundleTimes">次
                  </span>
                  <span class="price">
                    <label>折&ensp;扣&ensp;价:</label>
                    <input type="number" class="priceNum" v-model="card.bundleRealPrice">元/
                    <input type="number" class="times" v-model="card.bundleTimes">次
                  </span>
                  <label class="title">三、附加活动</label>
                  <textarea class="cardInfo"></textarea>
            </form>
        </div>
    </div>
</template>

<script>
import Qs from 'qs'
import axios from 'axios'
import { Toast } from 'mint-ui'
export default {
  name: 'myTimeCard',
  data () {
    return {
      card: {
        bundleNote: '', bundleOrigPrice: '', bundleRealPrice: '', bundleTimes: ''
      }
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    release () {
      if (this.card.bundleNote && this.card.bundleOrigPrice && this.card.bundleRealPrice) {
        axios.post('http://47.96.76.137:8088//bundleManageMent/bundleAdd', Qs.stringify({
          'bundleType': 1, 'bundleOrigPrice': this.card.bundleOrigPrice, 'bundleRealPrice': this.card.bundleRealPrice, 'bundleNote': this.card.bundleNote, 'bundleTimes': this.card.bundleTimes
        })).then(res => {
          console.log(res.data)
          if (res.data.status === '0') {
            Toast({
              message: '添加成功',
              duration: 1000
            })
            this.$router.go(-1)
          } else {
            Toast({
              message: '添加失败,请重新提交',
              duration: 1000
            })
          }
        })
      }
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.42667rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .usersTitle {
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .release {
    position: absolute;
    right: 0;
  }

  .timeCard {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .cardInfo {
    width: 8.66667rem;
    background: #f5f5f5;
    padding: 0.32rem 0.26667rem;
    border: none;
    resize: none;
    outline: none;
    border-radius: 0.2rem;
    height: 3.89333rem;
    font-size: 0.42667rem;
    color: #333333;
  }

  .title {
    display: inline-block;
    width: 100%;
    text-align: start;
    color: #333333;
    padding: 0.37333rem 0;
    font-size: 0.42667rem;
  }

  .price {
    display: flex;
    margin-left: 0.85rem;
    color: #333333;
    align-items: center;
    padding: 0.17333rem 0;
  }

  .price label {
    width: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #333333;
    font-size: 0.42667rem;
    flex-grow: 1 0 auto;
  }

  .price input {
    border: none;
    outline: none;
    border-bottom: 2px solid #f5f5f5;
    padding: 0.1rem 0;
    color: #ff8309;
  }

  .priceNum,
  .times {
    width: 2.1rem;
    text-align: center;
  }
</style>



